@import url("https://fonts.googleapis.com/css2?family=Libre+Bodoni:ital@0;1&display=swap");@import url("https://maxcdn.bootstrapcdn.com/font-awesome/4.2.0/css/font-awesome.min.css");*{box-sizing:border-box}body{margin:0}img{max-width:100%;height:auto}.kb-scope{--kb-vh: 1vh;--kb-mq-large: 'only screen and (max-width: 1280px)';--kb-mq-medium: 'only screen and (max-width: 1024px)';--kb-mq-small: 'only screen and (max-width: 768px)';--kb-mq-x-small: 'only screen and (max-width: 480px)';--kb-font-family-display: 'Libre Bodoni', serif;--kb-font-family-body: 'HelveticaNeue-Light', 'Helvetica Neue Light', 'Helvetica Neue', Helvetica, Arial, 'Lucida Grande', sans-serif;--kb-color-brand: #f68b1f;--kb-color-font-inverse: white;--kb-color-font-default: #1e1e1e;--kb-color-hero-background: #1e1e1e;height:calc(var(--kb-vh) * 100);font-family:var(--kb-font-family-body);font-size:1rem;font-weight:400;line-height:1.75;background-color:var(--kb-color-hero-background);color:var(--kb-color-font-inverse)}.kb-container{max-width:1280px;margin:0 auto}.kb-text-heading{font-family:var(--kb-font-family-display);font-weight:400;line-height:1.25;margin:1rem 0}.kb-text-heading--small{font-size:1.5rem}.kb-text-heading--medium{font-size:2.5rem}.kb-text-heading--large{font-size:3.5rem}.kb-font-color--brand{color:var(--kb-color-brand)}.kb-hero{display:flex;flex-wrap:wrap;justify-content:space-between;align-items:flex-end;padding:8rem}.kb-hero__content{flex:0 0 35rem}.kb-hero__sidebar{flex:0 0 auto}.kb-hero__logo{width:10rem;margin-bottom:2rem}
